.btn {
  padding: 10px;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  border-radius: 10px;
  color: var(--dark-color);
  background-image: linear-gradient(244deg, #C78853 33.49%, #D5BDA1 49.48%, #D9D9D9 53.82%, #EFE5D6 56.56%, #D2B589 77.34%);
  transition: transform .3s ease-in-out;

  &:focus {
    outline: none;
  }

  @include hover {
    transform: scale(1.1);
  }
}
