.link-list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-flow: row dense;
  align-items: center;
  gap: 20px;

  &__item {
    grid-column: 6 span;
    grid-auto-flow: row dense;
  }

  &__link {

    & svg {
      transition: transform .3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      filter: none;

      & svg {
        transform: scale(1.2);
      }
    }

    &:active:not(:focus-visible) svg {
      transition: transform .1s ease-in-out;
      transform: scale(1.1);
    // filter: brightness(0) saturate(100%) invert(58%) sepia(60%) saturate(372%) hue-rotate(346deg) brightness(89%) contrast(91%);
    }
  }

  &__link {
    filter: brightness(0) saturate(100%) invert(58%) sepia(60%) saturate(372%) hue-rotate(346deg) brightness(89%) contrast(91%);
  }
}

.link-list__link.focus-visible {
  outline: 2px solid var(--shamp-color);
  border-radius: 10px;
}
