.documents {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: var(--gray-color);

  &__container {
    display: flex;
    flex-direction: row-reverse;
  }

  &__title {
    margin: 0;
    margin-left: 23px;
    writing-mode: vertical-rl;
    text-align: center;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 500;
    line-height: 77px;
    text-transform: uppercase;
    color: #363636;
  }

  &__img {

    &-item:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__text {

    &-list {
      align-self: center;
    }

    &-item {
      padding-right: 20px;
    }

    &-item:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  &__text {
    margin: 0;
    font-size: 24px;
    font-weight: 400;
    color: var(--dark-color);
    transition: transform .3s ease-in-out, color .3s ease-in-out;
  }

  &__link {

    &:focus {
      outline: none;
    }

    @include hover {
      & p {
        color: var(--gold-color);
        transform: scale(1.05);
      }
    }
  }
}

.documents__link.focus-visible p {
  outline: 1px solid var(--dark-color);
  outline-offset: 5px;
  border-radius: 10px;
}
