.link {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -4px;
    display: block;
    width: 0;
    height: 1px;
    background-color: var(--dark-color);
    transform: translateX(-50%);
    transition: width .3s ease-in-out;
  }

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: -4px;
    display: block;
    width: 0;
    height: 1px;
    background-color: var(--dark-color);
    transform: translateX(-50%);
    transition: width .3s ease-in-out;
  }

  &--gold {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: -4px;
      display: block;
      width: 0;
      height: 1px;
      background-color: var(--shamp-color);
      transform: translateX(-50%);
      transition: width .3s ease-in-out;
    }

    // &::before {
    //   content: '';
    //   position: absolute;
    //   left: 50%;
    //   top: -4px;
    //   display: block;
    //   width: 0;
    //   height: 1px;
    //   background-color: var(--shamp-color);
    //   transform: translateX(-50%);
    //   transition: width .3s ease-in-out;
    // }

    &:focus {
      outline: none;
    }

    @include hover {

      &::after {
        width: 100%;
      }
    }

    &:active {
      color: var(--shamp-color);

      &::after {
        background-color: var(--shamp-color);
      }
    }
  }

  &:focus {
    outline: none;
  }

  @include hover {

    &::after,
    &::before {
      width: 120%;
    }
  }

  &:active {
    color: var(--shamp-color);

    &::after,
    &::before {
      background-color: var(--shamp-color);
    }
  }
}

.link.focus-visible {
  outline: 1px solid var(--dark-color);
  border-radius: 3px 3px 3px 3px;
  outline-offset: 3px;
}

.link--gold.focus-visible {
  outline: 1px solid var(--shamp-color);
  border-radius: 3px 3px 3px 3px;
  outline-offset: 3px;
}
